body {
  background-image: url("cat-bg.4a6cb775.jpg");
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
}

.is-hidden {
  visibility: hidden;
  pointer-events: none;
  display: none;
}

.breed-select {
  width: 250px;
  color: #fafafa;
  background-color: #202020;
  position: absolute;
  top: 5%;
  right: 10%;
}

.ss-content .ss-list .ss-option {
  color: #fafafa;
}

.ss-content .ss-list .ss-option:hover, .ss-content .ss-list .ss-option:focus, .ss-content .ss-list .ss-option.ss-highlighted, .ss-content .ss-list .ss-option:not(.ss-disabled).ss-selected {
  color: #202020;
  background-color: #fafafa;
}

.cat-info {
  max-width: 400px;
  color: #fafafa;
  background-color: rgba(32, 32, 32, .635);
  border: 2px solid #fafafa;
  margin: 50px 50px 50px 100px;
  padding: 30px;
  font-size: 18px;
  font-weight: 500;
}

.cat-img {
  max-width: 400px;
  margin: 0 auto;
  display: block;
}

.cat-description span, .cat-temperament span {
  font-size: 20px;
  font-weight: 700;
}

.loader {
  width: 150px;
  height: 20px;
  background: linear-gradient(#6df704 20px, rgba(0, 0, 0, 0)) no-repeat;
  border-radius: 24px;
  animation: custom 1s linear infinite;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: inset 10px 10px 30px -10px rgba(255, 255, 255, .627);
}

@keyframes custom {
  0% {
    background-position: -200px 0;
  }

  100% {
    background-position: 250px 0;
  }
}

